<template>
  <div class="login">
    <div class="log_box">
      <p>
        <label for="phone" class="iconfont">&#xe6a6;</label>
        <input type="text" v-model="phone" id="phone" @blur="regphone"  placeholder="请输入手机号码">
      </p>
      <p>
        <label for="password" class="iconfont">&#xe632;</label>
        <input type="text" name="" id="password" placeholder="请输入密码">
      </p>
      <div>
        <span class="le"></span>
        <span class="zhuce ri">还没有账号？点击<router-link :to="{ name:'Register' }">注册</router-link></span>
      </div>
      <button @click.prevent="gologin">登录</button>
    </div>
    <!-- <div class="xieyi">
      <p>点击注册代表您同意<span>《用户服务协议》</span></p>
    </div> -->
    <mt-popup v-model="popupVisible" position="top" modal=false>
      <h1 class="toptishi">meiyou</h1>
    </mt-popup>
  </div>
</template>

<script>
   import { Toast } from 'mint-ui';
  export default {
    name:'Login',
    data(){
      return{
        phone:'',
        popupVisible: false
        
      }
    },
    created(){
      
    },
    methods:{
      gologin(){
        localStorage.setItem("userphone", this.phone);
      },
      regphone(){
        var reg=/^[0-9]+.?[0-9]*$/;
        var phon=this.phone
        if(phon == "")
        {
          // alert("手机号不能为空");
          Toast({
            message: '手机号不能为空',
            duration: 5000
          });
          return false;
        }else if(!reg.test(phon))
        {
          Toast({
            message: '请输入正确的手机号',
            duration: 5000
          });
          return false;
        }else if(phon.length != 11)
        {
          Toast({
            message: '请输入正确的手机号',
            duration: 5000
          });
          return false;
        }
      }


      // getstrong(){
      //   if(localStorage.getItem("userphone")){
      //     this.aa=localStorage.getItem("userphone")
      //   }else{
      //     this.aa='没有null'
      //   }
      // }
    }

  }
   
</script>

<style scoped>
  .login{
    height: 100vh;
    /* background-color: cornflowerblue; */
    background: linear-gradient(to top,white,white,cornflowerblue);
    position: relative;
    padding-top: 200px;
  }
  .log_box{
    padding: 25px;
    /* background-color: white; */
    /* margin-top: 200px; */
  }
  .log_box label{
    color: rgb(59, 120, 235);
    margin-right: 10px;
    font-size: 1.3rem;
  }
  .log_box p{
    width: 100%;
    padding: 10px 0;
    border-bottom: 2px solid rgb(197, 215, 248);
    text-shadow: 0px 0px 2px white;
  }
  .log_box input{
    outline: none;
    border: 0;
    font-size: 0.875rem;
    /* color: #ddd; */
    background-color: transparent;
    
  }
  .log_box button{
    width: 100%;
    outline: none;
    border: 0;
    background-color: cornflowerblue;
    color: white;
    padding: 8px;
    border-radius: 50px;
    box-shadow: 0px 0px 4px cornflowerblue;
    margin-top: 20px;
  }

  .zhuce{
    padding: 10px;
    color: #aaa;
    font-size: 0.875rem;
  }
  .zhuce a{
    text-decoration: underline;
    color: #888;
    font-size: 0.875rem;
  }

  .xieyi{
    width: 100%;
    position: absolute;
    bottom: 20px;
    left: 0;
    text-align: center;
    
  }
  .xieyi p{
    width: 1005;
    
    color: #aaa;
    font-size: 0.75rem;
  }
  .xieyi span{
    font-size: 0.75rem;
    color: rgb(53, 116, 233);
  }


  .login >>> .toptishi{
    display: block;
    width: 100%;
    /* background-color: rgba(1, 1, 1, 0.1); */
    padding: 10px;
  }
  .login >>> .v-modal{
    height: 6%;
  }
</style>



